
.navbar-footer-background {
    background-color: #E8E8E8;
}

.navbar-border {
    border-bottom: #4286f4 2px solid;
}

.footer-border {
    border-top: #4286f4 2px solid;
}

.background {
    background-color: #F5F5F5;
}

.mainContent {
    height: 850px;
}

.contentView {
    overflow-y: scroll;
}

.myCardPic {
    border-radius: 5px;
}

.ContentSelection {
    padding: 4%;
}

.ContentSelection li {
    list-style-type: none;
}

.ContentSelection label {
    margin-bottom: 0;
}

.ContentSelection h4 {
    font-weight: 400;
    text-align: center;
    width: 100%;
}

li {
    padding-bottom: 0.75rem;
}

.ContentSelection h3 {
    margin-bottom: 0;
}
.Footer {
    width: 100%;
    text-align: center;
    display: inline-block;
}

.github-logo {
    opacity: 0.7;
    margin: 5px;
}

